import React from 'react'
import Container from '@mui/material/Container';
import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';
import profile from "../images/ctl.png";
import Getfree from "../Component/Getfree";

const Customers = () => {
  return (
    <>
        <section className="customer">
        <Container fixed>
            <div className="customer_wrap">
                <h2>What says our customers</h2>
            </div>
            <div className='clients'>
                <div className='ctlbox'>
                   <div className='arrowIcon Ifirst'><ArrowRightAltIcon/></div>
                   <div className='ctl_inner'>
                      <div className='profile'>
                         <div className='proIcon'><img className='pro_img' src={profile} alt="johan" /></div>
                         <div>
                            <h6>Aliqua</h6>
                            <span>Consectetur adipiscing</span>
                         </div>
                      </div>
                      <div className='content'>
                          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrudLorem ipsum</p>
                      </div>
                   </div>
                   <div className='arrowIcon'><ArrowRightAltIcon/></div>
                </div>
            </div>
            <Getfree/>
        </Container>
        </section>
    </>
  )
}

export default Customers;
