import React from 'react';
import Container from '@mui/material/Container';
import contImg from '../images/cont.jpg';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Navbar from '../Navbar';
import Footer from '../Component/Footer';

const Contactpage = () => {
  return (
  <>
          <Navbar/>
           <section className="contactUs">
           <Container fixed>
               <div className="contactUs_wrap">
                   <div className="contactUs_box padding">
                       <h2>Get in touch</h2>
                    <Grid container spacing={2}>
                        <Grid item md={6} xs={12}>
                            <TextField fullWidth className='contactInput' id="outlined-basic" label="First Name" variant="outlined" />
                        </Grid>
                        <Grid item md={6} xs={12}>
                            <TextField fullWidth className='contactInput' id="outlined-basic" label="Last Name" variant="outlined" />
                        </Grid>
                        <Grid item md={6} xs={12}>
                            <TextField fullWidth className='contactInput' id="outlined-basic" label="Contact Number" variant="outlined" />
                        </Grid>
                        <Grid item md={6} xs={12}>
                            <TextField fullWidth className='contactInput' id="outlined-basic" label="Email Id" variant="outlined" />
                        </Grid>
                    </Grid>
                      <Button className='readmore contactbtn' variant="contained">Send</Button>
                   </div>
                   <div className="contactUs_box">
                       <img className='img_fluid' src={contImg} alt="contact" />
                   </div>
               </div>
           </Container>
       </section>
       <Footer/>
  </>
  )
}

export default Contactpage;
