import React from 'react';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import wrokImg1 from '../images/work1.png';
import wrokImg2 from '../images/work2.png';
import wrokImg3 from '../images/work3.png';
import Footer from '../Component/Footer';
import Navbar from '../Navbar';

const Ourworkpage = () => {
  return (
   <>
     <Navbar/>
        <article className="work">
            <Container fixed>
               <div className="wrok_wrap">
                   <h4>Our work</h4>
               </div>
            </Container>
            <Container fixed>
            <Grid container spacing={3}>
                <Grid item md={4} xs={12}>
                    <Card variant="outlined">
                        <CardContent>
                           <img className='img_fluids' src={wrokImg1} alt=""  />
                        </CardContent>
                    </Card>
                 </Grid>
                 <Grid item md={4} xs={12}>
                    <Card variant="outlined">
                        <CardContent>
                           <img className='img_fluids' src={wrokImg2} alt=""  />
                        </CardContent>
                    </Card>
                 </Grid>
                 <Grid item md={4} xs={12}>
                    <Card variant="outlined">
                        <CardContent>
                           <img className='img_fluids' src={wrokImg3} alt=""  />
                        </CardContent>
                    </Card>
                 </Grid>
               

            </Grid>
            </Container>
        </article>
        <Footer/>
   </>
  )
}

export default Ourworkpage;
