import React from 'react'
import Button from '@mui/material/Button';

const Cards = (props) => {
  return (
     <>
        <div className="cards">
            <div className="card">
                <span>{props.icons}</span>
                <h6>{props.heading}</h6>
                <p>{props.discription}</p>
                <a className='abtn' href={props.links}>
                    <Button variant="text">Read More</Button>
                </a>
                
            </div>
        </div>
     </>
  )
}

export default Cards;
