import React from 'react'
import heroImg from '../images/hero.png';
import Button from '@mui/material/Button';
import Whatwedo from './Whatwedo';
import About from './About';
import Ourwork from './Ourwork';
import Best from './Best';
import Navbar from "../Navbar";
import Footer from "./Footer";
import Customers from './Customers';
import { useNavigate } from 'react-router-dom';

const Home = () => {

const navigate = useNavigate();

const goToAbout = () =>{
    navigate("/about");
}

const goToConatct = () =>{
    navigate("/contact");
}

  return (
      <>
      <Navbar/>
      <div className="hero">
          <div className="container">
              <div className="hero_wrap">
                  <div className="hero_box">
                  <h1>We Are Paint </h1>
                  <h2>Your Home</h2>
                  <p>adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco </p>
                  <div className="btngroup">
                     <Button className='readmore' variant="contained" onClick={() => goToAbout()}>Read More</Button>
                     <Button className='contact' variant="contained" onClick={()=> goToConatct()}>Contact Us</Button>
                  </div>
                  </div>
                  <div className="hero_box">
                      <img src={heroImg} alt="" />
                  </div>
              </div>
          </div>
      </div>
      <Whatwedo/>
      <About/>
      <Ourwork/>
      <Best/>    
      <Customers/>
      <Footer/>
      </>
  )
}

export default Home
