import React from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import MailIcon from '@mui/icons-material/Mail';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { NavLink } from "react-router-dom";




const Navbar = () => {
   
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };



  return (
     <>
         <header className="header">
             <div className="container">
                 <div className="navbar">
                     <div className='logo'>
                         <h2>Nerolac Paint</h2>
                     </div>
                     <div className='call'>
                         <p> <span><LocalPhoneIcon className='icons'/>Call : </span> (+91- 9112391531)</p>
                     </div>
                     <div className='email'>
                         <p> <span><MailIcon className='icons'/> email :</span> <a href="mailto:vinod.patil377@gmail.com">vinod.patil377@gmail.com</a></p>
                     </div>
                     <div className='hamburg' >
                     
                  
                        
                        <Button
                        id="basic-button"
                        aria-controls={open ? 'basic-menu' : undefined}
                        aria-haspopup="true"
                        aria-expanded={open ? 'true' : undefined}
                        onClick={handleClick}
                        >
                        <MenuIcon className='menu' color="primary" />
                        </Button>
                        <Menu 
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={open}
                        onClose={handleClose}
                        MenuListProps={{
                        'aria-labelledby': 'basic-button',
                        }}
                        >
                            <MenuItem onClick={handleClose}>
                               <NavLink className="nav" to="/">Home</NavLink>
                            </MenuItem>
                            <MenuItem onClick={handleClose}>
                                <NavLink className="nav" to="/about">About</NavLink>
                            </MenuItem>
                            <MenuItem onClick={handleClose}>
                               <NavLink className="nav" to="/ourwork">Our Work</NavLink>
                            </MenuItem>
                            <MenuItem onClick={handleClose}>
                               <NavLink className="nav" to="/whatwedo">What We Do</NavLink>
                            </MenuItem>
                            <MenuItem onClick={handleClose}>
                               <NavLink className="nav" to="/contact">Contact</NavLink>
                            </MenuItem>

                           
                        </Menu>
                      
                    
                 </div>
             </div>
             </div>
         </header>
     </>
  )
}

export default Navbar
