
import React from 'react'
import Container from '@mui/material/Container';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import PhoneIphoneIcon from '@mui/icons-material/PhoneIphone';
import MailIcon from '@mui/icons-material/Mail';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import InstagramIcon from '@mui/icons-material/Instagram';


const Footer = () => {
  return (
   <>
        <footer className="footer">
            <Container fixed>
                <div className="footer_wrap">
                    <p><span><LocationOnIcon/></span>Address <br/> <br/>97/7 Near Ram Mandir Pune 410046 </p>
                    <p><span><PhoneIphoneIcon/></span>(+91) 9112391531</p>
                    <p><span><MailIcon/></span><a href="mailto:vinod.patil377@gmail.com">vinod.patil377@gmail.com</a></p>
                </div>
                <div className='newsLetter'>
                    <h6>newsletter</h6>
                    <TextField className='inputId' fullWidth label="Enter Your Email"  color="warning" id="fullWidth outlined-basic" variant="outlined" />
                    <Button className='readmore subscribe' variant="contained">subs cribe</Button>
                    <h6>Follow us</h6>
                    <div className="social">
                        <ul>
                            <li><FacebookIcon className='socIcon'/></li>
                            <li><TwitterIcon className='socIcon'/></li>
                            <li><LinkedInIcon className='socIcon'/></li>
                            <li><InstagramIcon className='socIcon'/></li>
                        </ul>
                    </div>
                </div>
               
            </Container>

            <div className='footer_bottom'>
            <p>Copyright 2022 Developed by Vinod Patil</p>
            </div>
       </footer>
   </>
  )
}

export default Footer;
